.project-folder {
  width: 100%;
  padding: 60px 1rem;

  border-top-left-radius: 45px;
  border-bottom-right-radius: 45px;

  display: flex;
  align-items: flex-end;
  flex-direction: column;

  &:nth-child(odd) {
    background-color: $yellow;
  }

  &:nth-child(even) {
    background-color: $green;
  }

  &__title {
    margin-bottom: 1rem;
  }

  &__text-box {
    text-align: right;
    line-height: 1.4;
    margin-bottom: 1rem;
    max-width: 400px;

    & p {
      margin-bottom: 1rem;
    }
  }

  &__project {
    margin: 0 auto 2rem;
    transition: box-shadow 0.3s;

    &:hover,
    &:focus,
    &:active {
      box-shadow: 0 0 0 12px $black;
      outline: none;
    }
  }

  @media screen and (min-width: $mobile) {
    width: 425px;
  }

  @media screen and (min-width: $tablet) {
    width: 320px;
    margin: 0 auto;
  }
}
