html {
  box-sizing: border-box;
  font-size: 100%;
}

*,
*:before,
*:after {
  box-sizing: inherit;
  margin: 0;
  padding: 0;
}

body {
  background: #f9f9f9;
  width: 100%;
  height: 100%;
  position: relative;
}

html {
  font-family: "Raleway", sans-serif;
  color: #515151;
}

h1 {
  font-size: 2.25rem;
  font-weight: 700;
}

h2 {
  font-size: 1.5rem;
  font-weight: 700;
}

p {
  font-size: 1rem;
  font-weight: 400;
}

.italic {
  font-style: italic;
}

.burger-menu {
  border: none;
  background-color: transparent;
  cursor: pointer;
  display: block;
}
.burger-menu__bar {
  display: block;
  width: 40px;
  height: 3px;
  background-color: #515151;
}
.burger-menu__bar:nth-child(2) {
  background-color: #ff7575;
}
.burger-menu__bar:not(:last-of-type) {
  margin-bottom: 6px;
}

.nav-modal {
  width: 100vw;
  height: 100vw;
  border-radius: 50%;
  position: fixed;
  top: -50vw;
  right: -50vw;
  z-index: 100;
  background-color: #ff7575;
  transform: scale(0);
  transition: 0.6s;
}

.menu {
  list-style: none;
  display: block;
  position: fixed;
  z-index: 120;
  top: 50%;
  left: 50%;
  transform: translate(100vw, -50%);
  opacity: 0;
  transition: 0.8s;
}
.menu__item {
  padding: 1rem 0;
}
.menu__item a {
  text-decoration: none;
  color: #fff;
  cursor: pointer;
  transition: 0.3s;
}
.menu__item a:hover, .menu__item a:focus {
  color: #515151;
}

.btn {
  display: block;
  text-decoration: none;
  text-align: center;
  padding: 1rem 2rem;
  border-radius: 8px;
  color: #515151;
  font-size: 1.5rem;
  cursor: pointer;
}

.project {
  text-decoration: none;
  min-width: 280px;
  min-height: 224px;
  background-color: grey;
  border-radius: 8px;
  box-shadow: 0 0 0 12px #fff;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.project__header {
  margin-top: 12px;
  margin-left: -12px;
  background-color: #ff7575;
  color: #fff;
  padding: 5px;
  padding-left: 17px;
  width: 80%;
  border-top-right-radius: 8px;
  border-bottom-right-radius: 8px;
}
.project__tag-container {
  display: flex;
}
.project__tag {
  background-color: #515151;
  color: #fff;
  padding: 5px;
  border-radius: 8px;
  margin-left: 5px;
  margin-bottom: 12px;
}

.miniMaths {
  background-image: linear-gradient(to right, rgba(249, 249, 249, 0.6), rgba(249, 249, 249, 0.6)), url("img/miniMaths-5x4-500w.jpg");
  background-size: cover;
}

.wcicwt {
  background-image: linear-gradient(to right, rgba(249, 249, 249, 0.6), rgba(249, 249, 249, 0.6)), url("img/wcicwt-5x4-500w.jpg");
  background-size: cover;
}

.directory {
  background-image: linear-gradient(to right, rgba(249, 249, 249, 0.6), rgba(249, 249, 249, 0.6)), url("img/thEmployeeDirectory-5x4-500w.jpg");
  background-size: cover;
}

.covid {
  background-image: linear-gradient(to right, rgba(249, 249, 249, 0.6), rgba(249, 249, 249, 0.6)), url("img/ukCorona-5x4-500w.jpg");
  background-size: cover;
}

.animal {
  background-image: linear-gradient(to right, rgba(249, 249, 249, 0.6), rgba(249, 249, 249, 0.6)), url("img/svgSite-5x4-500w.jpg");
  background-size: cover;
}

.web-app {
  background-image: linear-gradient(to right, rgba(249, 249, 249, 0.6), rgba(249, 249, 249, 0.6)), url("img/webApp-5x4-500w.jpg");
  background-size: cover;
}

.tictactoe {
  background-image: linear-gradient(to right, rgba(249, 249, 249, 0.6), rgba(249, 249, 249, 0.6)), url("img/noughtsandCrosses-5x4-500w.jpg");
  background-size: cover;
}

.js-challenges {
  background-image: linear-gradient(to right, rgba(249, 249, 249, 0.6), rgba(249, 249, 249, 0.6)), url("img/codepenChallenges-5x4-500w.jpg");
  background-size: cover;
}

.react {
  background-image: linear-gradient(to right, rgba(249, 249, 249, 0.6), rgba(249, 249, 249, 0.6)), url("img/cvReact-5x4-500w.jpg");
  background-size: cover;
}

.api-gallery {
  background-image: linear-gradient(to right, rgba(249, 249, 249, 0.6), rgba(249, 249, 249, 0.6)), url("img/reactGallery-5x4-500w.jpg");
  background-size: cover;
}

.footer {
  display: grid;
  grid-row-gap: 30px;
  background-color: #515151;
  margin-top: 60px;
  padding: 60px 1rem;
  color: #f9f9f9;
  border-top-left-radius: 45px;
}
@media screen and (min-width: 425px) {
  .footer {
    justify-content: center;
  }
}
@media screen and (min-width: 768px) {
  .footer {
    grid-template-columns: repeat(2, 320px);
  }
}

.contact {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.contact__title {
  margin-bottom: 0.5rem;
}
.contact__email a:link, .contact__email a:visited {
  color: #f9f9f9;
  text-decoration: none;
  transition: 0.3s;
}
.contact__email a:hover, .contact__email a:active {
  color: #ff7575;
}

.social__title {
  margin-bottom: 0.5rem;
}
.social__icon-container {
  display: flex;
  justify-content: space-around;
  width: 80%;
  margin: 0 auto;
}
@media screen and (min-width: 425px) {
  .social__icon-container {
    width: 314px;
    margin: 0 39.5px;
  }
}
.social__icon {
  width: 40px;
  height: 40px;
  border-radius: 50%;
}
.social__icon a:link, .social__icon a:visited {
  width: 100%;
  height: 100%;
  display: grid;
  place-items: center;
  border-radius: 50%;
  color: #515151;
  background-color: #f9f9f9;
  text-decoration: none;
  transition: 0.3s;
}
.social__icon a:hover, .social__icon a:active {
  background-color: #ff7575;
}
.social__icon--color {
  fill: #515151;
}

.header {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  padding: 0.4rem 1rem;
  max-width: 1024px;
  margin: 0 auto;
}
.header__logo a:link, .header__logo a:visited {
  text-decoration: none;
  color: inherit;
}
.header__logo h2 span {
  color: #ff7575;
}

.hero {
  position: relative;
  height: 80vh;
  display: flex;
  flex-direction: column;
  overflow: hidden;
  max-width: 1024px;
  margin: 0 auto;
}
.hero__title {
  margin: 4rem 1rem 0;
}
.hero__blurb {
  margin: 1rem;
  width: 70%;
  max-width: 13.75em;
}
@media screen and (min-width: 425px) {
  .hero {
    justify-content: center;
    align-items: center;
  }
}

.arrow {
  width: 20px;
  height: 67px;
  margin-top: 80px;
  align-self: center;
  fill: #75ffa2;
  transition: 3.5s;
}

.yellow-container {
  position: absolute;
  top: 2.5rem;
  left: -1.7rem;
  width: 32vw;
  height: 23vw;
  background-color: #ffe875;
  border: 10px solid #fff;
  z-index: -10;
  transition: 1s;
}
@media screen and (min-width: 1024px) {
  .yellow-container {
    width: 328px;
    height: 236px;
    transform: translateX(1.7rem);
  }
}

.red-container {
  position: absolute;
  top: 3rem;
  right: -2rem;
  width: 40vw;
  height: 40vw;
  border-radius: 50%;
  background-color: #ff7575;
  border: 10px solid #fff;
  z-index: -10;
  transition: 1.5s;
}
@media screen and (min-width: 1024px) {
  .red-container {
    width: 410px;
    height: 410px;
    transform: translateX(-2rem);
  }
}

.project-folder {
  width: 100%;
  padding: 60px 1rem;
  border-top-left-radius: 45px;
  border-bottom-right-radius: 45px;
  display: flex;
  align-items: flex-end;
  flex-direction: column;
}
.project-folder:nth-child(odd) {
  background-color: #ffe875;
}
.project-folder:nth-child(even) {
  background-color: #75ffa2;
}
.project-folder__title {
  margin-bottom: 1rem;
}
.project-folder__text-box {
  text-align: right;
  line-height: 1.4;
  margin-bottom: 1rem;
  max-width: 400px;
}
.project-folder__text-box p {
  margin-bottom: 1rem;
}
.project-folder__project {
  margin: 0 auto 2rem;
  transition: box-shadow 0.3s;
}
.project-folder__project:hover, .project-folder__project:focus, .project-folder__project:active {
  box-shadow: 0 0 0 12px #515151;
  outline: none;
}
@media screen and (min-width: 425px) {
  .project-folder {
    width: 425px;
  }
}
@media screen and (min-width: 768px) {
  .project-folder {
    width: 320px;
    margin: 0 auto;
  }
}

.content-container {
  min-height: 100vh;
  display: grid;
  grid-template-rows: 1fr auto;
}

.main__project {
  display: grid;
  max-width: 1000px;
}
@media screen and (min-width: 768px) {
  .main__project {
    grid-template-columns: 4fr 1fr 3fr;
    grid-template-rows: 200px 200px;
    grid-column-gap: 12px;
    margin: 30px 12px 0;
  }
}
@media screen and (min-width: 1024px) {
  .main__project {
    margin: 30px auto 0;
  }
}

.project-page__project {
  margin: 2rem auto;
}
@media screen and (min-width: 768px) {
  .project-page__project {
    grid-row: 1/3;
    grid-column: 1/3;
    margin: 0;
  }
}

.project__tag-container--out {
  display: flex;
  flex-wrap: wrap;
  margin-left: 11px;
}
@media screen and (min-width: 768px) {
  .project__tag-container--out {
    grid-row: 1/2;
    grid-column: 3/4;
    font-size: 1.5rem;
    flex-direction: column;
    flex-wrap: nowrap;
    align-items: flex-start;
    margin-left: 20px;
  }
}

.brief {
  position: relative;
  background-color: #ffe875;
  margin: 60px 0;
  width: 100%;
  padding: 95px 1rem 60px;
  border-top-right-radius: 45px;
  border-bottom-left-radius: 45px;
}
.brief__title {
  position: absolute;
  top: 18px;
  z-index: 10;
}
.brief p {
  margin-bottom: 1rem;
}
@media screen and (min-width: 768px) {
  .brief {
    grid-row: 2/4;
    grid-column: 1/2;
    margin-bottom: 30px;
  }
}

.build {
  position: relative;
  background-color: #ff7575;
  margin: 60px 0;
  width: 100%;
  padding: 95px 1rem 60px;
  border-top-right-radius: 45px;
  border-bottom-left-radius: 45px;
}
.build__title {
  position: absolute;
  top: 18px;
  left: 16px;
  z-index: 10;
}
.build p {
  color: #fff;
  margin-bottom: 1rem;
}
@media screen and (min-width: 768px) {
  .build {
    grid-row: 4/5;
    grid-column: 1/-1;
    column-count: 2;
    margin-bottom: 30px;
  }
}

.red-circle {
  position: absolute;
  top: -15px;
  left: -15px;
  width: 90px;
  height: 90px;
  border-radius: 50%;
  background-color: #ff7575;
  border: 5px solid #fff;
  z-index: 5;
}

.yellow-square {
  position: absolute;
  top: -15px;
  left: -15px;
  width: 90px;
  height: 90px;
  background-color: #ffe875;
  border: 5px solid #fff;
  z-index: 5;
}

.btn--code {
  background-color: #ffe875;
  margin: 0 1rem 60px;
  transition: 0.3s;
}
.btn--code:hover {
  background-color: #ffe042;
}
@media screen and (min-width: 768px) {
  .btn--code {
    grid-row: 4/5;
    grid-column: 2/4;
    z-index: 1;
    align-self: flex-end;
    margin: 0 12px 40px 0;
  }
}

.btn--site {
  background-color: #75ffa2;
  margin: 0 1rem 60px;
  transition: 0.3s;
}
.btn--site:hover {
  background-color: #42ff80;
}
@media screen and (min-width: 768px) {
  .btn--site {
    grid-row: 3/4;
    grid-column: 2/4;
    align-self: flex-end;
    margin: 0 0 30px;
  }
}

.main__index {
  display: grid;
  grid-row-gap: 100px;
  max-width: 1320px;
}
@media screen and (min-width: 425px) {
  .main__index {
    justify-content: space-around;
  }
}
@media screen and (min-width: 768px) {
  .main__index {
    grid-template-columns: repeat(auto-fill, 320px);
    grid-column-gap: 20px;
    margin: 0 10px;
  }
}
@media screen and (min-width: 1024px) {
  .main__index {
    margin: 0 auto;
  }
}