.header {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  padding: 0.4rem 1rem;
  max-width: 1024px;
  margin: 0 auto;

  &__logo {
    & a:link,
    & a:visited {
      text-decoration: none;
      color: inherit;
    }

    & h2 span {
      color: $red;
    }
  }
}

.hero {
  position: relative;
  height: 80vh;
  display: flex;
  flex-direction: column;
  overflow: hidden;
  max-width: 1024px;
  margin: 0 auto;

  &__title {
    margin: 4rem 1rem 0;
  }

  &__blurb {
    margin: 1rem;
    width: 70%;
    max-width: 13.75em; // 220px / 16px = 13.75
  }

  @media screen and (min-width: $mobile) {
    justify-content: center;
    align-items: center;
  }
}

.arrow {
  width: 20px;
  height: 67px;
  margin-top: 80px;
  align-self: center;
  fill: $green;

  transition: 3.5s;
}

.yellow-container {
  position: absolute;
  top: 2.5rem;
  left: -1.7rem;

  width: 32vw;
  height: 23vw;

  background-color: $yellow;
  border: 10px solid #fff;
  z-index: -10;

  transition: 1s;

  @media screen and (min-width: $desktop) {
    width: 328px;
    height: 236px;

    transform: translateX(1.7rem);
  }
}

.red-container {
  position: absolute;
  top: 3rem;
  right: -2rem;

  width: 40vw;
  height: 40vw;
  border-radius: 50%;

  background-color: $red;
  border: 10px solid #fff;
  z-index: -10;

  transition: 1.5s;

  @media screen and (min-width: $desktop) {
    width: 410px;
    height: 410px;

    transform: translateX(-2rem);
  }
}
